.body{
  display: flex;
  flex-flow: column;
  min-height: 100vh;
  box-sizing: border-box
}

.main{
  flex: 1 1;
}

.app {
  margin-left: 30px;
  margin-top: 0px;
}

.appMain {
  background-color: rgba(210, 235, 13, 0.31) ;
  min-height: 500px;
}

footer {
  background-color: rgba(62, 135, 221, 0.79);
  align-items: flex-start;
  display: flex;
  flex-flow: column;
  color: #fff;
  margin-top: 20;
  height: 200px;
}

.footer-main {
  margin-left: 50px;
  height: 50px;
  width: 100%;
}

footer .footer-main a{
  color: #fff;
}

footer .footer-main h2{
  text-align: left;
  font-style: italic;
  font-family: Futura,Trebuchet MS,Arial,sans-serif;
}

footer .links {
  width: 100%;
  height: 150px;
  text-align: center;
}

footer .links img {
  margin-left: auto;
  margin-right: 20px;
  height: 40%;
  width: auto;

}

footer .links ul {
  list-style: none;
}

footer .links ul >*{
  cursor: pointer;
  display: inline-block;
  margin-left: 0px;
  margin-right: 50px;
  padding-bottom: 0px;
  border-bottom: 1px solid white;
}

.header {
  background-color: #fff;
  color: rgb(20, 155, 213);
  height: 90px;
}

.header-logo {
  float: left;
  width: 200px;
  padding: 10px
}

.header-logo button {
  float: left;
  display: inline;
  width: 100%;
}

.header-logo img {
  float: left;
  width: 100%;
}

.header-list button{
  cursor: pointer;
  float: left;
  border-style: none;
  background: none;
  font-size: 16px;
}

.header-list {
  float: right;
  padding-right: 10px;
}

.header-list li {
  float: left;
  list-style: none;
  padding: 0px 20px;
}

.title {
  font-family: sans-serif;
}

.contact-form input,textarea{
  width : 400px;
  margin-top : 10px;
  margin-bottom : 30px;
  padding : 20px;
  font-size : 18px;
  border : 1px solid #dee7ec;
}

.contact-form .submit{
  background-color : #dee7ec;
  color : #889eab;
}

.article {
  margin: 10px 30px
}

.news {
  list-style: none;
}

.news li button {
  cursor: pointer;
  border-style: none;
  background: none;
  text-align: left;
}

.members-card {
  height: 90px;
  width: 80%;
  height: auto;
  margin-left: auto;
  margin-right: auto;
}

.cards{
  width: 85%;
  height: 100%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.card {
  cursor : pointer;
  padding: 0px 0px;
  width: 220px;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
  flex-direction: column;
  border: 1px solid;
  border-radius: 4px;
  overflow: hidden;
  margin: 5px;
}

@media (max-width: 762px) {
  .cards{
    flex-direction: column;
  }
  .card{
    width: 80%;
    height: 30%;
    margin-left: auto;
    margin-right: auto;
  }
}

.card:hover{
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}

.card-header {
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  border-bottom: 1px solid;
  background-color: #FFEBEE;
  padding: 5px 0px;
}

.card-main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px 0;
}

.material-icons {
  font-size: 50px;
  margin-bottom: 5px;
}

.main-description {
  font-size: 20px;
  text-align: center;
}

/* Tomorrow Night Eighties Theme */
/* Original theme - https://github.com/chriskempson/tomorrow-theme */
/* http://jmblog.github.com/color-themes-for-google-code-highlightjs */
.tomorrow-comment, pre .comment, pre .title {
  color: #999999;
}

.tomorrow-red, pre .variable, pre .attribute, pre .tag, pre .regexp, pre .ruby .constant, pre .xml .tag .title, pre .xml .pi, pre .xml .doctype, pre .html .doctype, pre .css .id, pre .css .class, pre .css .pseudo {
  color: #f2777a;
}

.tomorrow-orange, pre .number, pre .preprocessor, pre .built_in, pre .literal, pre .params, pre .constant {
  color: #f99157;
}

.tomorrow-yellow, pre .class, pre .ruby .class .title, pre .css .rules .attribute {
  color: #ffcc66;
}

.tomorrow-green, pre .string, pre .value, pre .inheritance, pre .header, pre .ruby .symbol, pre .xml .cdata {
  color: #99cc99;
}

.tomorrow-aqua, pre .css .hexcolor {
  color: #66cccc;
}

.tomorrow-blue, pre .function, pre .python .decorator, pre .python .title, pre .ruby .function .title, pre .ruby .title .keyword, pre .perl .sub, pre .javascript .title, pre .coffeescript .title {
  color: #6699cc;
}

.tomorrow-purple, pre .keyword, pre .javascript .function {
  color: #cc99cc;
}

pre code {
  display: block;
  background: #2d2d2d;
  color: #cccccc;
  font-family: Menlo, Monaco, Consolas, monospace;
  line-height: 1.5;
  border: 1px solid #ccc;
  padding: 10px;
}

